.numbers_wrapper {    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-left: 100px;
    padding-right: 100px;
    background: url("../../assets/numbers_bg.png") center no-repeat;
    background-size: cover;
    color: var(--primary_white);
    position: relative;

    &::before {
        content: "";
        width: 100%;
        height: 100%;
        background: rgba(252, 195,2, 0.8);
        position: absolute;
    }
}

.numbers_container {
    margin-top: 150px;
    margin-bottom: 150px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1080px;
    z-index: 2;
}

.numbers_card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h3 {
        font-size: 30px;
    }
    .numbers_icon {
        padding: 30px;
    }
    .numbers_quantity {
        font-weight: 400;
    }
}

 // media query MOBILE
 @media (max-width: 431px) {
    .team_wrapper {
        padding-top: 20px;
        padding-bottom: 20px;
    }
    .numbers_container {
        flex-direction: column;
        margin-top: 20px;
        margin-bottom: 20px;
        text-align: center;
    }

    .numbers_card {
        margin-top: 20px;
        margin-bottom: 20px;
        .numbers_icon {
            width: 80px;
            padding: 10px;
        }

        h3 {
            font-size: 24px;
        }      
    }
}